import React, { type PropsWithChildren } from 'react';
import { createMedia, CreateMediaResult } from '../../libs/theme/create-media';

const WrappedMediaContext = /* @__PURE__ */ createMedia();

export const MediaProvider: React.FC<PropsWithChildren> = (props) => {
  const { children } = props;

  return <WrappedMediaContext.MediaContextProvider>{children}</WrappedMediaContext.MediaContextProvider>;
};

export const Media: React.FC<React.ComponentProps<CreateMediaResult['Media']>> = (props) => {
  return <WrappedMediaContext.Media {...props} />;
};
